@use "global" as *;

.p-gallery {
    padding-bottom: rem(100);
    background: $white;
}
.p-gallery__inner {
    width: 100%;
    max-width: rem(1370);
    margin: 0 auto;
    padding-bottom: rem(20);
    background: $white;
    @include mq(md) {
        max-width: 90%;
    }
}

.p-gallery__images:nth-of-type(1) {
    background: url(../images/common/info-1.jpg) no-repeat top center / cover;
}
.p-gallery__images:nth-of-type(2) {
    background: url(../images/common/info-2.jpg) no-repeat top center / cover;
}
.p-gallery__images:nth-of-type(3) {
    background: url(../images/common/info-3.jpg) no-repeat top center / cover;
}
.p-gallery__images:nth-of-type(4) {
    background: url(../images/common/info-4.jpg) no-repeat top center / cover;
}
.p-gallery__images:nth-of-type(5) {
    background: url(../images/common/info-5.jpg) no-repeat top center / cover;
}
.p-gallery__images:nth-of-type(6) {
    background: url(../images/common/info-6.jpg) no-repeat top center / cover;
}
.p-gallery__figure {
    display: grid;
    gap: rem(10);
    grid-template: 250px 250px / 1fr 1fr 1fr;
    background-color: $white;
    @include mq(md) {
        grid-template: 200px 200px 200px/ 1fr 1fr;
    }
    @media screen and (max-width: 415px) {
        grid-template: 200px 200px 200px 200px 200px 200px / 1fr;
    }
}
